.demo {
    margin: 0 auto 5rem;
    padding: 1rem;
    width: 1240px;
}

.demo .container {
    width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.demo .col-1 {
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.demo .col-1 p:nth-child(1) {
    font-size: 1.8rem;
    font-style: italic;
}

.demo .col-1 p:nth-child(2) {
    font-size: 3rem;
    font-weight: 600;
    margin: .5rem 0;
}

.demo .col-1 p:nth-child(3) {
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

.demo .col-2 {
    margin: auto;
}

@media screen and (max-width: 1240px) {
    .demo {
        width: 100%;
    }
    .demo .container {
        max-width: 695px;
        grid-template-columns: 1fr;
        grid-gap: 30px;
    }
    
    .demo iframe {
        width: 100%;
        height: auto;
    }
    .demo .col-1 p:nth-child(2) {
        font-size: 3rem;
        font-weight: 600;
        margin: 0.5rem 0 1rem;
    }
    .demo .col-1 p:nth-child(3) {
        font-size: 1.2rem;
        margin-bottom: 2rem;
    }
}



