.header {
    position: fixed;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: 0.3s ease-in;
    overflow: hidden; 
    background-color: rgba(0,0,0,.9);
}
.header .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1124px;
    margin: auto;
    height: 100%;
    padding: 0 1rem;
}
.header .navbar img {
    width: 250px;
    height: auto;
}
.header .nav-menu {
    display: flex;
}
.header .nav-item {
    padding: 1rem;
    font-weight: 500;
}
.header .nav-item a{
    color: #ffff;
}
.header .nav-item a:hover {
    padding-bottom: 12px;
    border-bottom:  3px solid var(--secondary-color);
}
.hamburguer {
    display: none;
    cursor: pointer;
}

@media screen and (max-width: 940px) {
    .header {
        max-width: 100%;
        background-color: rgba(0,0,0, 0.9);
    }
    .header .navbar{
        max-width: 100%;
    }
    .hamburguer {
        display: block;
    }
    .nav-menu {
        position: fixed;
        right: -100%;
        top: 90px;
        flex-direction: column;
        background-color: rgba(0,0,0, 0.9);
        width: 100%;
        height: 90vh;
        z-index: 999;
        text-align: center;
        transition: 0.3s;
    }
    .nav-menu.active {
        right: 0;
    }
    .nav-item{
        margin: 1.5rem 0;
    }
    .nav-item a {
        color: #ffff;

    }
    .header .navbar img{
        width: 150px;
    }

}






